import React from 'react';
import {useEffect} from 'react';

import {makeStyles} from '@material-ui/core/styles';

import {Card, CardHeader, Divider, CardContent, Grid, Button} from '@material-ui/core';

import {gridSpacing} from '../../store/constant';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import axios from 'axios';

import MuiTypography from '@material-ui/core/Typography';

import SubCard from '../../ui-component/cards/SubCard';

const useStyles = makeStyles({
    table: {
        minWidth: 350
    }
});

function createData(name, calories, fat, carbs, protein) {
    return {name, calories, fat, carbs, protein};
}

const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9)
];

export default function Consultations() {
    const classes = useStyles();
    const BASE_URL = 'https://backend-test.alsahaba.sa/api';
    const [users, setUsers] = React.useState([]);
    const [calls, setCalls] = React.useState([]);
    const [consultations, setConsultations] = React.useState([]);
    const [ratings, setRatings] = React.useState([]);
    const [selectedConsultation, setSelectedConsultation] = React.useState([]);

    const [visible, setVisibility] = React.useState(false);

    const fetchConsultations = async () => {
        await axios.get(`${BASE_URL}/consultations/`).then((response) => {
            setConsultations(response.data);
        });
    };
    const fetchRatings = async () => {
        await axios.get(`${BASE_URL}/ratings/`).then((response) => {
            setRatings(response.data);
        });
    };
    const fetchUsers = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setUsers(response.data);
        });
    };
    const fetchCalls = async () => {
        await axios.get(`${BASE_URL}/calls/`).then((response) => {
            setCalls(response.data);
        });
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const closePopup = () => {
        setVisibility(false);
    };
    const showSelectedConsultation = (consultaion) => {
        setVisibility(true);
        setSelectedConsultation(consultaion);
    };
    useEffect(() => {
        fetchConsultations();
        fetchRatings();
        fetchCalls();
        fetchUsers();
    }, []);
    return (
        <React.Fragment>
            {visible && <ShowSelectedConsultation consultation={selectedConsultation} handleClose={closePopup} />}
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Card>
                        <CardHeader title="All Consultations" />
                        <Divider />
                        <TableContainer>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>#</TableCell>
                                        <TableCell>Doctor name</TableCell>
                                        <TableCell align="left">Patient name</TableCell>
                                        <TableCell align="left">Date</TableCell>
                                        <TableCell align="left">Chief Complaint</TableCell>
                                        <TableCell align="left">History of illness</TableCell>
                                        <TableCell align="left">Review of systems</TableCell>
                                        <TableCell align="left">Examination</TableCell>
                                        <TableCell align="left">Assessment</TableCell>
                                        <TableCell align="left">Medication</TableCell>
                                        <TableCell align="left">Sick leave</TableCell>
                                        <TableCell align="left">Meeting ID</TableCell>
                                        <TableCell align="left">Follow Up Notes</TableCell>
                                        <TableCell align="left">Follow Up Doctor</TableCell>
                                        <TableCell align="left">Follow Up Date</TableCell>
                                        <TableCell align="left">Doctor's Rating</TableCell>
                                        <TableCell align="left">Patient's Rating</TableCell>
                                        <TableCell align="left">Patient's awaiting</TableCell>
                                        <TableCell align="left">Creation Date</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {consultations.map((consultation, index) => (
                                        <TableRow key={consultation.id} onClick={() => showSelectedConsultation(consultation)}>
                                            <TableCell align="left">{index + 1}</TableCell>
                                            <TableCell component="th">
                                                {users.find((user) => consultation.doctor_id === user.id) && (
                                                    <>
                                                        {users.find((user) => consultation.doctor_id === user.id).full_name}
                                                        <br />
                                                        <br />
                                                        ID/Iqama: {users.find((user) => consultation.doctor_id === user.id).iqama_number}
                                                    </>
                                                )}
                                            </TableCell>
                                            <TableCell align="left">
                                                {users.find((user) => consultation.patient_id === user.id) && (
                                                    <>
                                                        {users.find((user) => consultation.patient_id === user.id).full_name}
                                                        <br />
                                                        <br />
                                                        ID/Iqama: {users.find((user) => consultation.patient_id === user.id).iqama_number}
                                                    </>
                                                )}
                                            </TableCell>
                                            <TableCell align="left">{formatDate(consultation.created_at)}</TableCell>
                                            <TableCell align="left">{consultation.chief_complaint}</TableCell>
                                            <TableCell align="left">{consultation.history_of_illness}</TableCell>
                                            <TableCell align="left">{consultation.review_of_systems}</TableCell>
                                            <TableCell align="left">{consultation.examination}</TableCell>
                                            <TableCell align="left">{consultation.assessment}</TableCell>
                                            <TableCell align="left">{consultation.medication}</TableCell>
                                            <TableCell align="left">{consultation.sick_leave}</TableCell>
                                            <TableCell align="left">
                                                {calls.find((call) => consultation.call_id === call.id) ? (
                                                    <span>{calls.find((call) => consultation.call_id === call.id).meeting_id}</span>
                                                ) : (
                                                    <span>None</span>
                                                )}
                                            </TableCell>
                                            <TableCell align="left">{consultation.follow_up ? consultation.follow_up : 'None'}</TableCell>
                                            <TableCell align="left">
                                                {users.find((user) => consultation.follow_up_doctor_id === user.id) ? (
                                                    <>
                                                        {users.find((user) => consultation.follow_up_doctor_id === user.id).full_name}
                                                        <br />
                                                        <br />
                                                        ID/Iqama:{' '}
                                                        {users.find((user) => consultation.follow_up_doctor_id === user.id).iqama_number}
                                                    </>
                                                ) : (
                                                    'None'
                                                )}
                                            </TableCell>
                                            <TableCell align="left">
                                                {consultation.follow_up ? formatDate(consultation.updated_at) : 'None'}
                                            </TableCell>
                                            <TableCell align="left">
                                                {calls
                                                    .filter((call) => call.id === consultation.call_id)
                                                    .map((call) => {
                                                        const matchingRating = ratings.find(
                                                            (rating) => rating.call_id === call.id && rating.doctor_id > 0
                                                        );
                                                        return matchingRating ? (
                                                            <React.Fragment key={matchingRating.id}>
                                                                <span>{matchingRating.rating} star(s)</span>
                                                                <br />
                                                                <span>{matchingRating.message}</span>
                                                            </React.Fragment>
                                                        ) : null;
                                                    })}
                                            </TableCell>
                                            <TableCell align="left">
                                                {calls
                                                    .filter((call) => call.id === consultation.call_id)
                                                    .map((call) => {
                                                        const matchingRating = ratings.find(
                                                            (rating) => rating.call_id === call.id && rating.patient_id > 0
                                                        );
                                                        return matchingRating ? (
                                                            <React.Fragment key={matchingRating.id}>
                                                                <span>{matchingRating.rating} star(s)</span>
                                                                <br />
                                                                <span>{matchingRating.message}</span>
                                                            </React.Fragment>
                                                        ) : (
                                                            <span key={call.id}>none</span>
                                                        );
                                                    })}
                                            </TableCell>
                                            <TableCell align="left">
                                                {calls.find((call) => consultation.call_id === call.id) ? (
                                                    <span>
                                                        {calls.find((call) => consultation.call_id === call.id).awaiting_time} minute(s)
                                                    </span>
                                                ) : (
                                                    'None'
                                                )}
                                            </TableCell>
                                            <TableCell align="left">{formatDate(consultation.created_at)}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Card>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}

const ShowSelectedConsultation = (props) => {
    const BASE_URL = 'https://backend-test.alsahaba.sa/api';
    const [users, setUsers] = React.useState([]);
    const [calls, setCalls] = React.useState([]);

    const fetchUsers = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setUsers(response.data);
        });
    };
    const fetchCalls = async () => {
        await axios.get(`${BASE_URL}/calls/`).then((response) => {
            setCalls(response.data);
        });
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    useEffect(() => {
        fetchUsers();
        fetchCalls();
    }, []);
    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Card className="popup-container">
                    {console.log(props)}
                    <CardHeader title="Consultation" />
                    <Divider />
                    <CardContent className="popup-body">
                        <Button id="closeBtn" onClick={() => props.handleClose()}>
                            X
                        </Button>
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12} sm={6}>
                                <SubCard
                                    title={users.map((user) =>
                                        props.consultation.patient_id == user.id ? user.full_name + ' Consultation' : ''
                                    )}
                                >
                                    <Grid container direction="column" id="poppins" spacing={1}>
                                        <Grid item>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Doctor name: &nbsp;
                                                <span id="small-text">
                                                    {users.map((user) => (props.consultation.doctor_id == user.id ? user.full_name : ''))}
                                                </span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Patient name: &nbsp;
                                                <span id="small-text">
                                                    {users.map((user) => (props.consultation.patient_id == user.id ? user.full_name : ''))}
                                                </span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Date: &nbsp;
                                                <span id="small-text">{formatDate(props.consultation.created_at)}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Chief Complaint: &nbsp;
                                                <span id="small-text">{props.consultation.chief_complaint}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                History of illness: &nbsp;
                                                <span id="small-text">{props.consultation.history_of_illness}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Review of systems: &nbsp;
                                                <span id="small-text">{props.consultation.review_of_systems}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Examination: &nbsp;
                                                <span id="small-text">{props.consultation.examination}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Assessment: &nbsp;
                                                <span id="small-text">{props.consultation.assessment}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Sick leave: &nbsp;
                                                <span id="small-text">{props.consultation.sick_leave}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Meeting ID: &nbsp;
                                                <span id="small-text">{calls.find((call) => props?.consultation.call_id === call.id) ? (
                                                    <span>{calls.find((call) => props?.consultation.call_id === call.id).meeting_id}</span>
                                                ) : (
                                                    <span>None</span>
                                                )}</span>
                                            </MuiTypography>
                                            <MuiTypography variant="h3" gutterBottom>
                                                Last updated: &nbsp;
                                                <span id="small-text">{formatDate(props.consultation.updated_at)}</span>
                                            </MuiTypography>
                                        </Grid>
                                    </Grid>
                                </SubCard>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </Grid>
        </Grid>
    );
};
